.orderCode {
  line-height: 0.9;
  font-size: 18px;
}

.orderCode p {
  letter-spacing: 1px;
}

.borderBox {
  border: 1px solid #f3f3f3;
}

.orderTime {
  letter-spacing: 1px;
}
.reBox {
  padding-bottom: 15px;
}
.luckin {
  background-color: #a8d3fd;
  padding: 3px 10px;
  position: relative;
  width: 230px;
}
.luckinContent {
  display: flex;
  align-items: center;
}
.luckinContent h1 {
  font-size: 24px;
  font-weight: bold;
  font-family: monospace;
  margin-right: 3px;
}
.luckinName {
  margin-top: -6px;
}
.luckinTime {
  margin-top: 15px;
  font-size: 11px;
}
.luckinLogo {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
  filter: brightness(0);
}

.xicha {
  background-color: #f1e5b7;
  padding: 10px 15px;
  position: relative;
  width: 230px;
}
.xichaName {
  display: flex;
  align-items: center;
}
.xichaName span {
  font-size: 10px;
  border: 1px solid #000;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  display: inline-block;
  padding: 2px;
  line-height: 1;
  margin-right: 2px;
}
.xichaCode,
.xichaContent {
  font-size: 30px;
  font-weight: bold;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
  line-height: 1;
}
.xichaContent {
  font-size: 24px;
}
.xichaCus {
  line-height: 1.8;
}
.xichaMessage {
  padding: 20px 0 10px 0;
}
.xichaTip {
  background-color: #333;
  color: #fff;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  font-size: 12px;
  text-align: center;
}
.xichaFooter {
  display: flex;
  align-items: flex-end;
}
.xichaLeft {
  padding-top: 10px;
}
.xichaLeft h3 {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 2px;
  margin-top: -5px;
}
.xichaLeft p {
  font-size: 12px;
  font-weight: 400;
}
.xichaLeft small {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
}
.xichaRight {
  margin-top: 10px;
  /* margin-left: 5px; */
}

.xichaLogo {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 15px;
  right: 20px;
  right: 10px;
  /* filter: drop-shadow(2); */
}
.chabaidao {
  background-color: #fff;
  position: relative;
  width: 230px;
  padding: 5px;
}
.cbdHeader {
  display: flex;
  justify-content: space-between;
}
.cbdMode {
  margin-right: 40px;
}
.cbdLeft h3 {
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1.2px;
}
.cbdLeft p,
.cbdBody p,
.cbdFooterLeft p {
  line-height: 1;
}
.cbdContent {
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1.2px;
}
.cbdBody {
  margin-bottom: 40px;
}

.cbdFooter {
  display: flex;
}
.cbdFooterLeft {
  width: 75%;
}

.cbdStore {
  display: block;
}

.cbdTip {
  margin-top: 15px;
  padding-left: 10px;
  color: rgb(154, 225, 48);
  font-weight: 500;
}
.cbdLogo {
  width: 50px;
  height: 50px;
}

.goodme {
  background-color: #fff;
  position: relative;
  width: 230px;
  padding: 10px 8px 5px 8px;
  position: relative;
}
.gmCode {
  font-weight: bold;
  font-size: 24px;
}
.gmCode small {
  font-size: 12px;
}
.gmMode {
  background-color: #000;
  color: #fff;
  font-size: 12px;
  padding: 0px 3px;
  border-radius: 15px;
  display: inline-block;
  letter-spacing: 1px;
}
.gmHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
}
.gmContent {
  margin-bottom: 40px;
}
.gmContent h2 {
  font-size: 18px;
  font-weight: bold;
}
.gmBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gmBoxRight {
}

.gmTip {
  border-radius: 5px;
  border: 1px dashed #000;
  margin-top: 15px;
  padding: 16px 5px;
  position: relative;
  text-align: center;
}
.gmTip::before {
  content: '用户备注';
  position: absolute;
  background-color: #fff;
  left: 10px;
  top: -12px;
  padding: 0 2px;
}
.gmFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 10px;
}

.gmLogo {
  position: absolute;
  width: 50px;
  right: 10px;
}

.gmHi {
  width: 20px;
  display: inline;
  margin-right: 5px;
}
