.mainTemplate {
  border: 2px solid #f3f3f3;
  box-sizing: border-box;
  padding: 20px 50px;
  width: 80%;
  margin: 20px auto;
  border-radius: 25px;
  box-shadow: 0 0 3px 4px rgba(51, 51, 51, 0.05);
  position: relative;
  z-index: 1;
}
.content {
  display: none;
  transition: all 0.4s ease-in-out;
  /* transition-delay: 0.01s; */
  opacity: 0;
}
.content.contentShow {
  display: flex;
  cursor: pointer;
  opacity: 1;
}
.mainTemplate img {
  transition: all 0.2s ease-in-out;
  /* transform: scale(0.8); */
  margin-right: 50px;
  /* box-shadow: 0 0 3px 4px rgba(51, 51, 51, 0.05); */
}

.mainTemplate .content:hover img {
  transform: translateY(-60px) scale(1.1);
  /* transform: scale(1.1); */
}
.mainTemplate .content:hover h1 span {
  animation: imgScale 0.3s ease-in-out;
}
.mainTemplate .content:hover .tempBtn {
  opacity: 1;
}

@keyframes imgScale {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-15deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(10deg);
  }
  90% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.tempMenus {
  position: absolute;
  left: 50px;
  top: -30px;
  z-index: -1;
  display: flex;
  /* flex-direction: column; */
  /* align-items: flex-end; */
}

.tempMenus > div {
  width: auto;
  padding: 0 10px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  font-size: 18px;
  margin-bottom: 10px;
  box-shadow: 0 0 10px 4px rgba(51, 51, 51, 0.1);
  z-index: -1;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  margin-right: 5px;
}
.tempMenus > div.active {
  /* width: 60px; */
  background-color: #333;
  color: #fff;
  height: 40px;
  line-height: 40px;
  transform: translateY(-10px);
}

.whyBox {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 30px;
}
.whyBoxImg {
  width: 33%;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  /* transform-origin: bottom; */
}
.whyBoxImg:nth-child(1),
.whyBoxImg:nth-child(3) {
  transform: scale(0.9);
}
.whyBoxImg:nth-child(2) {
  margin: 0 -120px;
  z-index: 10;
  /* transform: translateY(10px); */
}
.whyBoxImg:hover {
  transform: scale(1.1);
  z-index: 14;
}

.detail {
  width: 1329px;
  position: relative;
  margin: 0 auto;
  margin-bottom: 40px;
}
.detailMsg {
  position: absolute;
  width: 580px;
  /* background-color: #f3f3f3; */
  left: 69.2px;
  top: 580px;
  font-family: monospace;
  font-size: 16px;
  text-align: left;
  line-height: 1.8;
}
.detailNum {
  width: 280px;
  background-color: #f3f3f3;
  height: 190px;
  position: absolute;
  right: 69.2px;
  top: 45.68px;
  border-radius: 25px;
  box-sizing: border-box;
  padding: 15px;
  text-align: left;
  overflow: hidden;
}
.subscribeBtn {
  width: 280px;
  background-color: #333;
  height: 190px;
  margin-left: -15px;
  position: relative;
  top: 0px;
  transition: all 0.2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.subscribeBtn > div {
  /* width: 100px; */
  padding: 10px 20px;
  text-align: center;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 40px;
  font-size: 18px;
  cursor: pointer;
}
.subscribeBtn > div:hover {
  background-color: #fff;
  color: #333;
}

.detailNum > span {
  font-style: italic;
  font-size: 16px;
  border: 1px solid #333;
  padding: 3px 10px;
  border-radius: 30px;
  font-weight: bold;
}

.detailNum > div {
  height: 120px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.detailNum > div p {
  margin-right: 10px;
}
.detailNum:hover .subscribeBtn {
  transform: translateY(-170px);
}

.stepBox {
  margin: 40px auto;
  width: 1000px;
}
.stepBox .stepLabel {
  padding: 5px 40px;
}
.stepBox li {
  padding: 20px 0;
  font-size: 22px;
  cursor: pointer;
  /* border-radius: 15px; */
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  background-color: #333;
  color: #fff;
  height: 550px;
}
.stepBox li h2 {
  /* text-align: left; */
  /* padding-left: 300px; */
  background-color: #fff;
  color: #333;
  padding: 2px 5px;
  border-radius: 5px;
  width: 70%;
  margin: 0 auto;
}
.stepBox li h2 span {
  font-weight: bold;
  font-family: monospace;
  background-color: #333;
  color: #fff;
  padding: 2px 5px;
  border-radius: 5px;
  margin-right: 5px;
}
.stepBox > li:hover {
  background-color: #333;
  color: #fff;
  height: auto;
}
.stepBox > li:hover h2 span {
  background-color: #fff;
  color: #333;
}

.stepDetail {
  /* height: 0;
  opacity: 0; */
  color: #fff;
  /* border-top: 1px dashed #fff; */
}
.stepBox > li:hover .stepDetail {
  height: auto;
  padding-top: 20px;
  opacity: 1;
}

@media (max-width: 750px) {
  .detail {
    width: 100%;
  }
  .detailNum {
    left: 14px;
    width: 118px;
    top: 25px;
    height: 175px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .detailNum > div {
    height: auto;
    display: flex;
    flex-direction: column;
    height: 80px;
    margin-top: 20px;
  }
  .subscribeBtn,
  .detailMsg {
    display: none;
  }
  .mainTemplate {
    width: 100%;
    padding: 15px;
  }
  .tempMenus {
    top: -40px;
    left: 50px;
  }
  .tempMenus > div {
    width: 45px;
    height: 40px;
    border-top-left-radius: 45px;
    border-bottom-left-radius: 0;
    border-top-right-radius: 45px;
    line-height: 40px;
    overflow: hidden;
  }
  .tempMenus > div.active {
    /* width: 45px; */
    height: 50px;
    line-height: 50px;
  }
  .content.contentShow {
    display: block;
  }
  .whyBox {
    width: 100%;
  }
  .whyBoxImg {
    width: 50%;
  }
  .stepBox {
    width: 100%;
  }
  .stepBox .stepLabel {
    padding: 5px;
  }
  .stepDetail {
    height: auto;
  }
  .stepBox li {
    /* height: auto; */
    height: 400px;
  }
}

.ipad {
  position: absolute;
  right: -100px;
  /* z-index: 99; */
  width: 80%;
  top: 10%;
  transform: rotate3d(10,-5,-1, 10deg);
  opacity: 0.98;
  transition: all .05s ease-in-out infinite;
}
.ipad:hover {
  width: 88%;
  transform: rotate3d(10, 20, 10);
}