.detailNav {
  width: 300px;
}
.homeNav {
  width: 180px;
  /* height: 120px; */
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
  background-color: rgb(245 245 245);
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
  position: relative;
  height: 130px;
  overflow: hidden;
  /* cursor: pointer;
  box-shadow: rgba(47, 84, 235, 0.12) 0px 6px 12px;
  background-color: #fff; */
}
.homeNav h1 {
  font-size: 96px;
  text-align: center;
  margin-bottom: 20px;
}
.homeNav h1 img {
  margin: 0 auto;
  width: 40px;
}
.homeNav .name p {
  font-size: 18px;
}
.detailDesc {
  color: #888;
  font-size: 14px;
  margin-top: 5px;
  text-align: left;
  display: none;
}

.homeNav:hover {
  width: 400px;
  /* height: 200px;
  width: 200px;
  border-radius: 20px;
  transform: translateY(-5px); */
}

.homeNav:hover .name {
  display: flex;
  font-weight: bold;
  align-items: center;
}
.homeNav:hover .name h1 {
  font-size: 18px;
  margin-right: 10px;
  margin-bottom: 0;
}
.homeNav:hover .name h1 img {
  width: 20px;
}

.homeNav:hover .detailDesc {
  display: block;
}

.navBtn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  opacity: 0;
}

.homeNav:hover .navBtn {
  opacity: 1;
}

.homeNav_badge {
  /* opacity: 0; */
  position: absolute;
  top: 5px;
  right: 2px;
}

.pointWrapper {
  position: absolute;
  z-index: -999;
}
.pointWrapper i {
  display: inline-block;
  border-radius: 100%;
  position: absolute;
  /* opacity: 0; */
}
.point0 {
  background: rgb(47, 84, 235);
  border-color: rgb(47, 84, 235);
  width: 10px;
  height: 10px;
  opacity: 0.4;
  top: 20px;
  right: -60px;
}

.point1 {
  width: 10px;
  height: 10px;
  border: 1px solid rgb(47, 84, 235);
  background: transparent;
  opacity: 0.4;
  top: 20px;
  right: -60px;
}

.homeNav:hover .pointWrapper i {
  /* opacity: 0.4; */
}

.homeNav:hover .point0 {
  right: 20px;
  top: -50px;
  transition: all ease-in-out 0.5s forwards;
}
.homeNav:hover .point1 {
  right: -60px;
  top: -60px;
  transition: all ease-in-out 0.5s forwards;
}
