.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
}
.testimonial {
  width: 30%;
  margin: 40px 0;
  display: flex;
  align-items: center;
  position: relative;
}
.testimonial:hover {
  transform: translateY(-10px);
  transition: all 0.15s ease-in-out;
}
.info {
  min-width: 80px;
  max-width: 100px;
  position: absolute;
  left: 100px;
  top: -30px;
  z-index: 100;
}
.info .infoAvatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: 0 1px 10px rgba(100, 100, 100, 0.15);
}
.quoteBox {
  padding: 20px 25px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 1px 10px rgba(100, 100, 100, 0.15);
  position: relative;
  margin-left: 30px;
  max-width: 750px;
  z-index: 0;
  padding-top: 80px;
  height: 240px;
}
